
.MenuBar {
    position: fixed;
    top: 60px;
    left: 0px;
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
    width: 260px;
    height: 100%;
    background-color: white;
    z-index: 999;
    border-right: 1px solid #eee;
}
.MenuBar h5 {
    text-align: left;
    padding-left: 20px;
    padding-top: 20px;
    padding-bottom: 30px;
    border-bottom: 1px solid #ccc;
}

.MenuBar div {
    position: relative;
}
.MenuBar > div > span {
    position: absolute;
    top: 51px;
    right: 140px;
    font-size: 13px;
}
.MenuBar ul{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
}

.MenuBar ul li {
    display: block;
    height: 50px;
    line-height: 50px;
    background-color: white;
}
.MenuBar ul li a {
    display: inline-block;
    width: 100%;
    color: Black;
    text-decoration: none;
    padding-left: 20px;
    font-weight: 600;
    font-size: 0.9rem;
    
}

.MenuBar ul li a:hover{
    background-color: #eee;
    text-decoration: none;

}
.SubMenu-collapse{
    margin-left: 30px;

}

.SubMenu-root{
    display: none !important;
}

.DropDownMenu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.DropDownMenu:hover {
    background-color: #eee;
}
.DropDownMenu > span {
    display: inline-block;
    width: 50px;
    height: 50px;
    cursor: pointer;
}


